import React, { Component } from "react";
import { updateRenderState } from ".";
import SearchPage from "./searchPage";

export default class Diary extends Component {
	mountSearchComponent = (ar, en) => {
		updateRenderState(<SearchPage key={Math.random() * 100} title={ar} />);
		window.history.pushState(undefined, undefined, "/search#" + en);
	};

	render() {
		return (
			<div id='pageBody'>
				<div className='mealName'>
					<h3 ></h3>
				</div>
				<div className='mealFoods' onClick={_ => this.mountSearchComponent("", "breakfast")}>
					<div className='mealFoodsImgP'>
						<img src='./assets/bread.png' alt='bread' />
						<p> </p>
					</div>
				</div>
				<div className='mealName'>
					<h3></h3>
				</div>
				<div className='mealFoods' onClick={_ => this.mountSearchComponent("", "lunch")}>
					<div className='mealFoodsImgP'>
						<img src='./assets/soup.png' alt='soup' />
						<p> </p>
					</div>
				</div>
				<div className='mealName'>
					<h3></h3>
				</div>
				<div className='mealFoods' onClick={_ => this.mountSearchComponent("", "dinner")}>
					<div className='mealFoodsImgP'>
						<img src='./assets/fish.png' alt='fish' />
						<p> </p>
					</div>
				</div>
				<div className='mealName'>
					<h3> </h3>
				</div>
				<div className='mealFoods' onClick={_ => this.mountSearchComponent(" ", "snacks")}>
					<div className='mealFoodsImgP'>
						<img src='./assets/banana.png' alt='banana' />
						<p> </p>
					</div>
				</div>
			</div>
		);
	}
}
